.Landing {
  display: flex;
  flex-direction: column;
}

.Landing .section-a {
  background-image: url(/Portafolio/static/media/patagonia.3ab2ebb1.jpg);
  background-size: cover;
  background-position: center;
  color: white;
}
.Landing .section-a h1 {
  text-align: center;
  margin-bottom: 0;
}
.Landing .section-a .description {
  margin: 0 auto;
  text-align: justify;
}
.Landing .section-a .contact {
  display: flex;
  margin: 20px auto;
}
.Landing .section-a .contact .contact-item {
  flex: 1 1;
  text-align: center;
  transition: transform 150ms linear;
}
.Landing .section-a .contact .contact-item svg {
  font-size: 30px;
  color: white;
}
.Landing .section-a .contact .contact-item:hover {
  transform: translateY(-5px);
}
.Landing .section-a .down {
  position: absolute;
  bottom: 0;
  left: calc(50% - 28px);
}
.Landing .section-a .down svg {
  font-size: 56px;
  color: white;
}

.section-b .skills-container {
  width: 90%;
  margin: 0 auto;
  margin-bottom: 55px;
  display: flex;
  flex-wrap: wrap;
}
.section-b .skills-container .skill {
  text-align: center;
}
.section-b .skills-container .skill .skill-description {
  text-align: justify;
}

.section-c {
  background-color: #171A25;
  padding: 30px;
}
.section-c .message-alert {
  position: fixed;
  top: 5vh;
  left: 40%;
  z-index: 99 !important;
  width: 20%;
}
.section-c h1 {
  color: white;
}
.section-c .contact-form {
  margin: 0 auto;
}
.section-c .contact-form .double-input {
  display: flex;
  width: 100%;
}
.section-c .contact-form .double-input .field {
  flex: 1 1;
  margin: 0;
}
.section-c .contact-form .field {
  margin: 3vh 0;
  width: 100%;
}
.section-c .contact-form .field .form-label {
  display: flex;
  align-items: center;
  color: #DADADA;
  margin: 5px 0;
}
.section-c .contact-form .field .form-label p {
  margin-left: 7px;
}
.section-c .contact-form .field .form-input {
  width: 100%;
  border-radius: 7px;
  border: none;
  background-color: #DADADA;
  padding: 10px 15px;
}
.section-c .contact-form .submit {
  text-align: center;
}
.section-c .contact-form .submit button {
  width: 30%;
  margin: 10px 0;
}

@media screen and (min-width: 800px) {
  .Landing .section-a {
    height: 100vh;
  }
  .Landing .section-a .space {
    height: 37vh;
  }
  .Landing .section-a h1 {
    font-size: 5vw;
  }
  .Landing .section-a .description {
    width: 30%;
    font-size: 1.5vw;
  }
  .Landing .section-a .contact {
    width: 15%;
  }

  .section-c {
    -webkit-clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 0, 50% 10%);
            clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 0, 50% 10%);
  }
  .section-c h1 {
    margin-top: 90px;
  }
  .section-c .contact-form {
    width: 35vw;
  }
  .section-c .contact-form .double-input #mail {
    margin-left: 10px;
  }
}

@media (min-width: 1500px) {
  .Landing .section-a .space {
    height: 43vh;
  }
  .Landing .section-a .contact .contact-item svg {
    font-size: 40px;
  }
  .Landing .section-a .down {
    left: calc(50% - 38px);
  }
  .Landing .section-a .down  svg {
    font-size: 76px;
  }
}

