.projects-container {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.projects-container .project {
  position: relative;
  padding: 0;
}
.projects-container .project .logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px 10px 0 0;
}
.projects-container .project .information {
  padding: 10px 20px;
}
.projects-container .project .information .tags {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.projects-container .project .information .tags .tag {
  background-color: #506C64;
  color: #F1F1F1;
  border-radius: 10px;
  padding: 5px 10px;
  margin: 5px 7px;
}
.projects-container .project .information .description {
  margin: 20px 10px;
  font-size: 17px;
  text-align: justify;
}
.projects-container .project .information .actions {
  margin: 0 auto;
  display: flex;
  justify-content: center;
}
.projects-container .project .information .actions a {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin: 0 5px;
}
.projects-container .project .information .actions a svg {
  margin-left: 10px;
}

