@media screen and (max-width: 800px) {
  .navbar .nav-left .logo {
    font-size: 35px;
  }
  .navbar .nav-right {
    position: absolute;
    top: 10vh;
    left: 0;
    width: 100vw;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: transform linear 300ms;
    background-color: #171A25;
  }
  .navbar .nav-right .nav-contact {
    flex: 1 1;
    display: flex;
    justify-content: space-between;
    width: 100px;
  }
  .navbar .nav-right .nav-contact a svg {
    font-size: 30px;
    color: white;
  }
  .navbar .nav-menu svg {
    font-size: 35px;
  }

  .section-title {
    font-size: 50px;
    margin: 25px 0;
  }
  .section-title::after {
    width: 15%;
  }
}

@media screen and (min-width: 800px) {
  .navbar .nav-left .logo {
    font-size: 50px;
  }
  .navbar .nav-right {
    display: flex;
  }
  .navbar .nav-right .nav-contact {
    display: none;
  }
  .navbar .nav-menu {
    display: none;
  }

  .section-title {
    font-size: 55px;
  }
  .section-title::after {
    width: 6%;
  }
}
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #F4F4F4;
  color: black;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}

/* Container por big screens */
.container {
  max-width: 1450px;
  margin: 0 auto;
}

/* Navbar */
.navbar {
  width: 100vw;
  padding: 10px 5vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 0;
  z-index: 99 !important;
  transition: linear 300ms;
}
.navbar .nav-left {
  font-family: 'Pacifico', cursive;
}
.navbar .nav-left .logo {
  text-decoration: none;
  font-weight: bold;
}
.navbar .nav-right .nav-link {
  flex: 1 1;
  text-decoration: none;
  text-align: center;
  font-size: 25px;
  padding: 10px 15px;
  margin: 0 10px;
  transition: linear 200ms;
}
.navbar .nav-right .nav-link:hover {
  transform: scale(1.05);
}
.navbar .nav-right .active {
  text-decoration: underline;
}

/* Seaction Title */
.section-title {
  text-align: center;
}
.section-title::after {
  content: "";
  display: block;
  border-bottom: 4px solid #4C86A8;
  margin: 0 auto;
}

/* Main Button */
.main-button {
  padding: 10px 15px;
  border-radius: 5px;
  background: rgb(221,76,76);
  background: linear-gradient(90deg, rgba(221,76,76,1) 0%, rgba(215,38,61,1) 100%); 
  color: #DADADA;
  border: none;
}
.main-button:hover {
  box-shadow: -5px 5px 5px #0000003f;
}

/* transparent button */
.transparent-button {
  background-color: transparent;
  border: none;
}

/* Card containers */
.card {
  background-color: #DADADA;
  box-shadow: 0 0 20px #9c9c9c;
  flex: 1 1;
  margin: 20px;
  border-radius: 10px;
  padding: 10px 20px;
}

